/* General Page Container */
.getInvolvedPage {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    font-family: Arial, sans-serif;
  }
  
  /* Event Title */
  .getInvolvedPage h3 {
    font-size: 24px;
    margin: 10px 0;
    text-align: center;
    font-weight: bold;
  }
  
  /* Event URL */
  .getInvolvedPage h4 a {
    font-size: 16px;
    color: #065f7d;
    text-decoration: none;
    display: block;
    text-align: center;
    margin: 5px 0;
  }
  
  /* Event Date */
  .getInvolvedPage p {
    font-size: 14px;
    color: #555;
    text-align: center;
    margin: 10px 0;
  }
  
  /* Image Styling */
  .getInvolvedPage .img {
    width: 100%;
    height: auto;
    margin: 15px 0;
    object-fit: cover;
  }
  
  /* Event Description */
  .getInvolvedPage p {
    font-size: 16px;
    color: #333;
    line-height: 1.6;
    text-align: justify;
    margin-bottom: 20px;
  }
  
  /* Blog Content Elements */
  .getInvolvedPage h4, .getInvolvedPage h5 {
    margin: 20px auto;
    font-size: 18px;
    font-weight: bold;
    color: #333;
    width: 100%;
  }
  
  .getInvolvedPage p {
    width: 100%;
    margin: 20px auto;
    font-size: 16px;
    color: #555;
    line-height: 1.6;
  }
  
  /* Quote Styling */
  .getInvolvedPage .quote {
    background-color: lightblue;
    font-style: italic;
    padding: 14px;
    border-left: 5px solid #065f7d;
    margin: 20px auto;
    font-size: 16px;
  }
  
  /* Note Styling */
  .getInvolvedPage .note {
    background-color: #fddede;
    font-style: italic;
    padding: 14px;
    border-left: 5px solid #eb4438;
    margin: 20px auto;
    font-size: 16px;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .getInvolvedPage {
      padding: 15px;
    }
  
    .getInvolvedPage h3 {
      font-size: 22px;
    }
  
    .getInvolvedPage h4 a {
      font-size: 14px;
    }
  
    .getInvolvedPage p, .getInvolvedPage h4, .getInvolvedPage h5 {
      font-size: 14px;
    }
  
    .getInvolvedPage .img {
      width: 100%;
      height: auto;
    }
  }
  